@use "@styles/settings" as *;

.bl_sliderArrow {
  position: absolute;
  z-index: 2;
  inset: 0;
  pointer-events: none;
}

.bl_sliderArrow_prev,
.bl_sliderArrow_next {
  position: absolute;
  top: 0;
  bottom: 0;
  display: block;
  margin-block: auto;
  width: 60px;
  height: 60px;
  background-image: url('/common/slide_arrow.svg');
  background-size: 60px 60px;
  background-repeat: no-repeat;
  pointer-events: auto;
}

.bl_sliderArrow_prev {
  left: -24px;
  rotate: 180deg;
}

.bl_sliderArrow_next {
  right: -24px;
}